@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap);
/* Styles go here */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.w1200 {
    width: 1000px;
    margin: 0rem auto;
}

.App {
    font-family: "Poppins", sans-serif;
}

a {
    text-decoration: none;
}

nav,
ul {
    list-style: none;
}

body {
    background-color: #ec1a56;
}

@media screen and (max-width: 1050px) {
    .w1200 {
        width: 95%;
    }
}
.burger-icon {
  display: none;
}
.header {
  background-color: #fff;
  padding: 1rem 3rem;
}
.header-content,
.header-content nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header-content nav > * {
  color: #3a3939;
}
.header-content nav > * + * {
  margin-left: 1rem;
}
.btn {
  background-color: #ec1a56;
  border: 1px solid #ec1a56;
  color: #fff;
  padding: 0.8rem 1.3rem;
  display: flex;
  align-items: center;
  height: 60px;
}
.btn a {
  color: #fff;
}
.header-content nav img {
  width: 80px;
}
.header-content nav .btn img {
  width: 30px;
}

.header-content nav > .btn {
  color: #fff;
}
.header-content > img {
  width: 60px;
}
.Hero {
  background-color: #eff2fe;
  background-position: center;
  background-size: cover;
}
.Hero .content {
  color: #3a3939;
  display: grid;
  grid-gap: 3rem;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  height: 88vh;
}
.Hero .content img {
  width: 100%;
}
.Hero .content h1 {
  font-size: 4rem;
}
.Hero .content p {
  margin-top: 1rem;
}
.form-wrapper-area {
  padding: 10rem 0rem;
}
.FormWrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 550px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
}
.FormWrapper > * {
  padding: 2rem;
}
.FormWrapper .left_side {
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  width: 100%;

  flex-direction: column;
}
.FormWrapper .left_side > * {
  width: 100%;
}
.FormWrapper .left_side .logo {
  position: absolute;
  top: 20px;
  left: 30px;
  width: 50px;
}
.FormWrapper .left_side h1 {
  color: #3a3939;
  font-size: 2.5rem;
  margin-bottom: 1rem;
}
#upload_file_label {
  width: 100%;
  display: flex;
  height: 35px;
  background-color: #ec1a56;
  color: #fff;
  align-items: center;
  justify-content: center;
  margin-top: 1rem;
  border-radius: 99px;
  cursor: pointer;
}
.FormWrapper .right_side {
  background-color: #ededf0;
  display: flex;
  align-items: center;
  justify-content: center;
}
.FormWrapper .right_side img {
  width: 100%;
}

@media screen and (max-width: 1050px) {
  .Hero .content {
    grid-template-columns: 1fr;
    grid-gap: 0rem;
    justify-items: center;
  }
  .Hero .content > img {
    display: none;
  }
  .Hero .content > div {
    text-align: center;
  }
}

@media screen and (max-width: 850px) {
  .FormWrapper {
    grid-template-columns: 1fr;
  }
  .FormWrapper .right_side {
    display: none;
  }
}
@media screen and (max-width: 650px) {
  .burger-icon {
    display: block;
  }
  .header-content nav {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    flex-direction: column;
    width: 60%;
    padding: 3rem 0rem;
    transition: 0.3s ease all;
    left: -100%;
    overflow: auto;
    z-index: 99;
    background-color: #fff;
  }
  .header-content nav.active {
    left: 0;
  }
}
@media screen and (max-width: 550px) {
  .Hero .content h1 {
    font-size: 3rem;
  }
}
@media screen and (max-width: 450px) {
  .FormWrapper .left_side h1 {
    font-size: 2rem;
  }
}
@media screen and (max-width: 360px) {
  .Hero .content h1 {
    font-size: 2rem;
  }
}

